import React, {useState} from 'react';
import style_profileInfo from "./ProfileInfo.module.css";
import {createField, elementFormHOC} from "../../Second_files/Forms_control/formsControl";
import {reduxForm} from "redux-form";
import {maxLengthThunkCreator, required} from "../../Second_files/Validators/validators";

const Input = elementFormHOC('input');
const maxLength20 = maxLengthThunkCreator(20);

const Form = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            {createField('fullName', 'fullName', Input, [required, maxLength20], null, null)}
            {createField(null, 'lookingForAJob', Input, null,
                {type: 'checkbox'}, 'lookingForAJob')}
            {createField('lookingForAJobDescription', 'lookingForAJobDescription', Input, null,
                null, null)}
            {createField('github', 'github', Input, null, null, null)}
            {createField('vk', 'vk', Input, null, null, null)}
            {createField('facebook', 'facebook', Input, null, null, null)}
            {createField('instagram', 'instagram', Input, null, null, null)}
            {createField('twitter', 'twitter', Input, null, null, null)}
            {createField('website', 'website', Input, null, null, null)}
            {createField('youtube', 'youtube', Input, null, null, null)}
            {createField('mainLink', 'mainLink', Input, null, null, null)}
            <button>Submit</button>
        </form>
    )
};

const ReduxForm = reduxForm({form: 'newAboutMe'})(Form);


const ProfileJobAndContacts = (props) => {

    let [editMod, setEditMod] = useState(false);

    const changeContactInfo = () => {
        setEditMod(true);
    };

    const onSubmit = (formData) => {
        debugger
        let contacts = {
            github: formData.github,
            vk: formData.vk,
            facebook: formData.facebook,
            instagram: formData.instagram,
            twitter: formData.twitter,
            website: formData.website,
            youtube: formData.youtube,
            mainLink: formData.mainLink
        };
        debugger
        props.saveChangeAboutMe(props.userId, formData.lookingForAJob, formData.lookingForAJobDescription, formData.fullName, contacts);
        setEditMod(false);
    };

    if (editMod) {
        return (
            <ReduxForm onSubmit={onSubmit}/>
        )
    }

    return (
        <>
            <div>
                <div>About me</div>
                <div className={style_profileInfo.aboutMe}>
                    {props.profilePage.aboutMe || 'Пока нет инфы'}
                </div>
            </div>
            <div><p><b>job search:</b> {props.profilePage.lookingForAJob ? 'Yes' : 'No'}</p></div>
            <div><p><b>work preferences:</b> {props.profilePage.lookingForAJobDescription || 'No'}</p></div>
            <div>My contacts</div>
            <div><p><b>Github:</b> {props.profilePage.contacts.github || 'НЕТ'}</p></div>
            <div><p><b>Vk:</b> {props.profilePage.contacts.vk || 'НЕТ'}</p></div>
            <div><p><b>Facebook:</b> {props.profilePage.contacts.facebook || 'НЕТ'}</p></div>
            <div><p><b>Instagram:</b> {props.profilePage.contacts.instagram || 'НЕТ'}</p></div>
            <div><p><b>Twitter:</b> {props.profilePage.contacts.twitter || 'НЕТ'}</p></div>
            <div><p><b>Website:</b> {props.profilePage.contacts.website || 'НЕТ'}</p></div>
            <div><p><b>Youtube:</b> {props.profilePage.contacts.youtube || 'НЕТ'}</p></div>
            <div><p><b>mainLink:</b> {props.profilePage.contacts.mainLink || 'НЕТ'}</p></div>
            {!props.isOwner && <button onClick={changeContactInfo}>change contact info</button>}
        </>
    )
};


export default ProfileJobAndContacts;