import React from 'react';
import style_profileInfo from './ProfileInfo.module.css';
import userDefaultPhoto from "../../../assets/images/userProfile.png";
import ProfileStatusWithHook from "./ProfileStatusWithHook";
import ProfileJobAndContacts from "./ProfileJobAndContacts";

const ProfileInfo = (props) => {

    console.log('PROFILE_INFO');

    const imageDownload = (ev) => {
        if (ev.target.files.length) {
            props.saveImage(ev.target.files[0]);
        }
    };

    return (
        <div className={style_profileInfo.content}>

            <div className={style_profileInfo.ImageAvatarka}>
                <div>
                    <img src={props.profilePage.photos.large || userDefaultPhoto} alt=''/>
                </div>
                <div>
                    {!props.isOwner && <input type={"file"} onChange={imageDownload}/>}
                </div>
                <div>
                    <p>{props.profilePage.fullName}</p>
                </div>
            </div>

            <div className={style_profileInfo.Right}>

                <div className={style_profileInfo.status}>
                    <ProfileStatusWithHook status={props.profilePageStatus}
                                           updateStatusThunk={props.updateStatusThunk}/>
                </div>

                <div className={style_profileInfo.contacts}>
                    <ProfileJobAndContacts profilePage={props.profilePage}
                                           isOwner={props.isOwner}
                                           userId={props.userId}
                                           saveChangeAboutMe={props.saveChangeAboutMe}/>
                </div>

            </div>

        </div>
    );
};

export default ProfileInfo;